import React from 'react';
import { graphql, StaticQuery } from 'gatsby';


const postQuery = graphql`
  query {
    allMarkdownRemark{
      edges {
        node {
          id
          frontmatter {
            title
            date
            author
            path
          }
          excerpt
        }
      }
    }
  }
`;


